import "./styles.css";
import moment from "moment";

export default function App() {
  const startDate = moment().startOf("week");
  const endDate = moment("2023-03-10");
  const arrangementStart = {
    date: moment("2022-12-26"),
    peopleIndex: 0
  };
  const people = ["Henry", "Ken", "Leo", "Yogurt", "Nana"];
  const totalViews = [];
  let weekViews = [];
  while (startDate.isBefore(moment(endDate).endOf("week"))) {
    // const currentDate = startDate.add(1, "days");
    const today = {
      date: startDate.format("YYYY-MM-DD"),
      dayISO: startDate.format("ddd"),
      day: startDate.day(),
      people: startDate.isBefore(moment(endDate))
        ? people[startDate.diff(arrangementStart.date, "day") % 5]
        : null
    };
    weekViews.push(today);
    if (today.day === 6) {
      totalViews.push(weekViews);
      weekViews = [];
    }
    startDate.add(1, "days");
  }

  return (
    <div className="App">
      <h3>Days remaining: {Math.abs(moment().diff(endDate, "days"))}</h3>
      <table>
        <thead>
          <tr>
            <th>Sun</th>
            <th>Mon</th>
            <th>Tue</th>
            <th>Wed</th>
            <th>Thu</th>
            <th>Fri</th>
            <th>Sat</th>
          </tr>
        </thead>
        <tbody>
          {totalViews.map((week, i) => (
            <tr key={i}>
              {week.map((day, j) => (
                <td
                  key={j}
                  className={
                    moment(day.date).isSame(moment(), "day")
                      ? "active"
                      : moment(day.date).isBefore(moment())
                      ? "past"
                      : "future"
                  }
                >
                  <div className="day">
                    <div className="header">
                      {moment(day.date).format("MMM DD")}
                    </div>
                    <div className="people">{day.people}</div>
                  </div>
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
